import { Container, Typography } from '@mui/material';

const PrivacyPolicy = () => {
  return (
    <Container component="main" maxWidth="md" sx={{ mt: 4 }}>
      <Typography variant="h4" gutterBottom>
        Poltica de Privacidade
      </Typography>
      <Typography variant="body1" paragraph>
        A ML Vipe Shop respeita a sua privacidade e est comprometida em proteger os dados pessoais que coletamos sobre voc. Esta poltica descreve as prticas de informaes para o site da ML Vipe Shop, incluindo que tipo de dados so coletados, como esses dados so usados e protegidos, e as escolhas que voc tem em relao ao uso de seus dados.
      </Typography>

      <Typography variant="h6" gutterBottom>
        Coleta de Dados Pessoais
      </Typography>
      <Typography variant="body1" paragraph>
        Coletamos informaes que voc fornece diretamente a ns ao criar uma conta, realizar uma compra ou se comunicar conosco. Isso pode incluir, mas no est limitado a, seu nome, endereo de email, endereo postal, nmero de telefone e informaes de pagamento.
      </Typography>

      <Typography variant="h6" gutterBottom>
        Uso de Dados
      </Typography>
      <Typography variant="body1" paragraph>
        Usamos as informaes coletadas para fornecer, manter, proteger e melhorar nossos servios, para desenvolver novos servios e para proteger a ns e nossos usurios. Tambm usamos essas informaes para oferecer contedo personalizado  como mostrar-lhe produtos ou anncios que achamos que podem interessar-lhe.
      </Typography>

      <Typography variant="h6" gutterBottom>
        Compartilhamento de Informaes
      </Typography>
      <Typography variant="body1" paragraph>
        Compartilhamos informaes pessoais com empresas, organizaes ou indivduos externos  ML Vipe Shop quando temos seu consentimento para faz-lo. Exigimos um acordo de confidencialidade de nossos servios parceiros para garantir a segurana de seus dados pessoais.
      </Typography>

      <Typography variant="h6" gutterBottom>
        Segurana dos Dados
      </Typography>
      <Typography variant="body1" paragraph>
        Tomamos medidas apropriadas para proteger contra acesso no autorizado ou alterao, divulgao ou destruio de dados. Isso inclui, entre outras medidas, criptografia de dados durante a transmisso e armazenamento, e sistemas de segurana para prevenir acessos no autorizados aos nossos sistemas.
      </Typography>

      <Typography variant="h6" gutterBottom>
        Alteraes na Poltica de Privacidade
      </Typography>
      <Typography variant="body1" paragraph>
        Nossa Poltica de Privacidade pode mudar de tempos em tempos. No reduziremos seus direitos sob esta Poltica de Privacidade sem o seu consentimento explcito. Publicaremos quaisquer alteraes de poltica de privacidade nesta pgina e, se as alteraes forem significativas, forneceremos um aviso mais destacado.
      </Typography>

      <Typography variant="h6" gutterBottom>
        Contato
      </Typography>
      <Typography variant="body1" paragraph>
        Se voc tem quaisquer perguntas sobre esta Poltica de Privacidade ou o tratamento de seus dados pessoais, por favor entre em contato conosco enviando um email para contato@mlvipeshop.com.br.
      </Typography>
    </Container>
  );
};

export default PrivacyPolicy;